import "./styles.css";
import { useState, useRef } from "react";

const COUNTER_INIT_STATE = 0;

export default function App() {
  const [counter, setCounter] = useState(COUNTER_INIT_STATE);
  const counterIntercalRef = useRef();
  const startCounter = () => {
    if (counterIntercalRef.current) return;
    counterIntercalRef.current = setInterval(() => {
      setCounter((prevState) => prevState + 1);
    }, 1000);
  };

  const pauseCounter = () => {
    clearInterval(counterIntercalRef.current);
    counterIntercalRef.current = null;
  };

  const resetCounter = () => {
    clearInterval(counterIntercalRef.current);
    setCounter(COUNTER_INIT_STATE);
    counterIntercalRef.current = null;
  };

  return (
    <div className="App">
      <h1>Counter {counter}</h1>
      <button
        style={{ marginRight: "8px" }}
        onClick={!counterIntercalRef.current ? startCounter : pauseCounter}
      >
        {!counterIntercalRef.current ? "Start" : "Pause"}
      </button>
      <button onClick={resetCounter}>Reset</button>
    </div>
  );
}
